@use "base/variables" as v;
@use "base/mixins" as m;

.formulario {
    textarea {
        height: 20rem;
    }

    input:not([type="submit"]), 
    textarea,
    select {
        padding: 1rem;
        display: block;
        width: 100%;
        background-color: v.$blanco;
        border: 1px solid v.$gris;
        border-radius: 1rem;
        margin-bottom: 2rem;
    }

    input[type=radio] {
        margin: 0;
        width: auto;
    }

    legend {
        font-size: 2rem;
        color: #333;
    }

    label {
        font-weight: v.$bold;
        text-transform: uppercase;
        display: block;
    }

    p {
        font-size: 1.4rem;
        color: #333;
        margin: 2rem 0 0;
    }
}

.forma-contacto {
    display: flex;
    max-width: 30rem;
    justify-content: space-between;
    align-items: center;
}

.boton-verde {
    @include m.boton(v.$verde, inline-block);
}